<template>
  <div>
    <input
      type="search"
      name=""
      id=""
      @keyup="checkEnter($event)"
      v-model="searchText"
    />
    <button @click="doSearch">조회</button>
    <p>key enter 호출</p>
    <input
      type="search"
      name=""
      id=""
      @keyup.enter="doSearch"
      v-model="searchText"
    />
    <p>{{ enterValue }}</p>
  </div>
</template>
<script>
// .enter, .down, .delete, .esc, .left, .right, .space, .tab, ....
// .stop : event.stopPropagetion()
export default {
  components: {},
  data() {
    return {
      searchText: '',
      enterValue: ''
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    doSearch() {
      console.log(this.searchText)
      this.enterValue = 'Enter Value : ' + this.searchText
    },
    checkEnter(event) {
      if (event.keyCode === 13) {
        this.doSearch()
      }
    }
  }
}
</script>
